<template>
  <div class="index">
    <search-box :searchData="searchData" :table="table"></search-box>
    <div class="btn-block">
      <div class="btn-main-deepen" @click="onSubjectClick">竞赛科目维护</div>
      <div class="btn-main-deepen ml15" @click="onDialogClick">添加学生</div>
    </div>

    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="10%">姓名</th>
        <th width="10%">性别</th>
        <th width="10%">年级</th>
        <th width="10%">班级</th>
        <th width="8%">学生性质</th>
        <th width="12%">出生日期</th>
        <th width="8%">年龄</th>
        <th width="10%">籍贯</th>
        <th width="12%">竞赛科目</th>
        <th width="10%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.studentName}}</td>
        <td>{{$enums.Sex.getName(item.sex)}}</td>
        <td>{{item.grade}}</td>
        <td>{{item.aliasName}}</td>
        <td>{{$enums.StudentNature.getName(item.studentNature)}}</td>
        <td>{{item.birthday | substrTime}}</td>
        <td>{{item.age}}</td>
        <td>{{item.nativePlace}}</td>
        <td>{{item.subjectName}}</td>
        <td>
          <span @click="$router.push(`/research-orsay/orsayTraining/student-detail?id=`+item.studentId+``)" class="oper">查看</span>
          <span @click="remove(item)" class="oper">删除</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
    <el-dialog title="添加学生" class="dialog"
               :visible.sync="dialogVisible"
               width="700px"
               center>
      <el-form v-if="dialogVisible" label-width="60px" label-position="left">
        <el-form-item label="科目:">
          <el-select class="input" v-model="studentForm.subjectId" placeholder="请选择">
            <el-option
                    v-for="(item, idx) in subject"
                    :key="idx"
                    :label="item.label"
                    :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="学生:">
          <el-tag class="mr10" @close="selectedName.splice(key,1);studentForm.studentId.splice(key,1)" :key="key"
                  v-for="(item,key) in selectedName" closable>{{item}}
          </el-tag>
          <el-input disabled v-show="!selectedName.length" class="input" placeholder="请选择学生"></el-input>
        </el-form-item>
        <p class="separated-text">选择学生</p>
        <div class="search-box">
          <div class="item">
            <span class="text">年级/班级</span>
            <el-cascader
                    @change="changeClassCascade"
                    clearable
                    :options="classCascade"
                    change-on-select></el-cascader>
          </div>
          <div class="item">
            <span class="text">关键字</span>
            <el-input @keyup.enter.native="getStudent()" v-model="studentQuery.keywords" placeholder="请输入姓名"></el-input>
          </div>
        </div>

        <resize-table>
        <table class="table student-table mt15">
          <thead>
          <tr>
            <th width="10%">选择</th>
            <th width="11%">姓名</th>
            <th width="11%">性别</th>
            <th width="17%">年级</th>
            <th width="17%">班级</th>
            <th width="17%">学生性质</th>
            <th width="17%">籍贯</th>
          </tr></thead>
          <tbody>
          <tr v-for="item in studentList">
            <td>
              <el-checkbox :value="studentForm.studentId.includes(item.id)" @change="changeCheck(item)"></el-checkbox>
            </td>
            <td>{{item.name}}</td>
            <td>{{$enums.Sex.getName(item.sex)}}</td>
            <td>{{item.gradeName}}</td>
            <td>{{item.aliasName}}</td>
            <td>{{$enums.StudentNature.getName(item.studentNature)}}</td>
            <td>{{item.nativePlace}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!studentList.length"></no-data>
        <el-pagination
                class="pagination"
                layout="prev, pager, next"
                @current-change="getStudent"
                :page-size="10"
                :total="studentTotal">
        </el-pagination>
      </el-form>
      <div class="btns_two">
        <div @click="addStudentFun" class="btn-submit btn-student mr15">提交</div>
        <div @click="dialogVisible = false" class="btn-clear">返回</div>
      </div>
    </el-dialog>
    <!--竞赛科目维护-->
    <el-dialog title="竞赛科目维护"
               :visible.sync="dialogSubjectVisible"
               width="40%"
               center>

      <resize-table>
      <table class="tableSubject table">
        <thead>
        <tr>
          <th width="50%">科目名称</th>
          <th width="50%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in CompetitionSubject">
          <td>
            <el-input size="small" style="width: 80%" v-show="item.status" v-model="ediSubject"></el-input>
            <span v-show="!item.status">{{item.label}}</span>
          </td>
          <td>
            <span class="oper"
                  @click="CompetitionSubject.forEach(e=> {e.status = false}); item.status = true; ediSubject=item.label"
                  v-show="!item.status">编辑</span>
            <span @click="removeSubject(item)" class="oper" v-show="!item.status">删除</span>
            <span class="add oper" v-show="item.status" @click="onSubmitEdi(item)">确定</span>
            <span class="oper" v-show="item.status" @click="item.status = false">取消</span>
          </td>
        </tr>
        <tr>
          <td colspan="2" style="text-align: left">
            <el-input
                    placeholder="请输入科目名称"
                    size="small"
                    v-show="!subjectSwitch"
                    v-model="newSubject"
                    style="width: 68%; margin-right: 30px; margin-left: 10px"></el-input>
            <span class="add" @click="newSubject = '';subjectSwitch = false" v-show="subjectSwitch"
                  style="display: block;text-align: center">添加科目</span>
            <span @click="onSubmitSubject" v-show="!subjectSwitch" class="add mr10">提交</span>
            <span @click="subjectSwitch = true" v-show="!subjectSwitch" class="add">取消</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <div class="btns_two">
        <div class="btn-submit" @click="dialogSubjectVisible=false">返回</div>
      </div>
    </el-dialog>
  </div>

</template>
<script>
  export default {
    data () {
      return {
        searchData: [
          {type: 'select', title: '性别', model: 'sex', option: this.$enums.Sex.list},
          {type: 'cascader', title: '年级/班级', modelName: ['gradeId', 'classId'], option: []},
          {type: 'select', title: '竞赛科目', model: 'subjectId', option: []},
          {type: 'input', placeholder: '姓名/籍贯'},
        ],
        table: {
          url: '/competition/queryCompetitionPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            // 关键字
            keywords: null,
            // 性别
            sex: null,
            // 年级
            gradeId: null,
            // 班级
            classId: null,
            // 科目
            subjectId: null,
          }
        },
        dialogTable: {
          url: '/notice/queryNoticeReadRecordPage.do',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        dialogVisible: false,
        dialogSubjectVisible: false,
        title: null,
        switch: false,
        switchOne: true,
        // 维护科目
        CompetitionSubject: [],
        subject: '',
        subjectSwitch: true,
        // 编辑
        editSubjectSwitch: true,
        // 新增科目名称
        newSubject: null,
        // 编辑
        ediSubject: null,
        /* 查询学生 */
        // 学生列表
        studentList: [],
        // 学生总数 用于分页
        studentTotal: 0,
        // 条件
        studentQuery: {
          gradeId: null,
          classId: null,
          keywords: ''
        },
        // 添加竞赛生表单
        studentForm: {
          subjectId: null,
          studentId: [],
        },
        // 选中的竞赛生名字
        selectedName: [],
        // 年级班级 级联列表
        classCascade: [],
      }
    },
    watch: {
      dialogSubjectVisible: function () {
        if (!this.dialogSubjectVisible) {
          this.subjectSwitch = true
          this.CompetitionSubject.forEach(e => {
            e.status = false
          })
        }
      }
    },
    methods: {
      // 选择学生
      changeCheck (item) {
        let index = this.studentForm.studentId.indexOf(item.id)
        if (index !== -1) {
          this.selectedName.splice(index, 1)
          this.studentForm.studentId.splice(index, 1)
        } else {
          this.selectedName.push(item.name)
          this.studentForm.studentId.push(item.id)
        }
      },
      // 获取学生
      async getStudent (pageNo = 1) {
        let res = await this.$ajax({
          url: '/student/queryStudentPageByAtSchool.do',
          query: {
            classId: this.studentQuery.classId,
            gradeId: this.studentQuery.gradeId,
            keywords: this.studentQuery.keywords,
            pageNo,
            pageSize: 10,
          },
        })
        this.studentTotal = res.total
        this.studentList = res.list
      },
      // 获取年级班级 级联列表
      async getClassCascade () {
        this.classCascade = await this.$ajax({
          url: '/class/queryGradeClassCascade.do'
        })
      },
      // 查询学生切换班级的回调
      changeClassCascade (val) {
        this.studentQuery.gradeId = val[0]
        this.studentQuery.classId = val[1] || null
        this.getStudent()
      },
      // 添加学生
      async addStudentFun () {
        await this.$ajax({
          url: '/competition/addCompetition.do',
          el: '.btn-student',
          query: this.studentForm
        })
        this.$remind('添加成功')
        this.dialogVisible = false
        this.$search(this.table)
      },
      // 竞赛课目
      async onSubjectList () {
        let arr = await this.$ajax({
          url: '/competitionSubject/queryCompetitionSubjectList.do',
        })
        arr = arr.map(item => {
          return {
            value: item.id,
            label: item.subjectName,
            status: false
          }
        })
        this.CompetitionSubject = arr
        this.searchData[2].option = arr
        this.subject = arr
      },
      // 获取年级
      async onGetGradeList () {
        let arr = await this.$ajax({
          url: '/class/queryGradeClassCascade.do'
        })
        this.searchData[1].option = []
        arr.forEach(async (e, i) => {
          this.searchData[1].option.push({
            label: e.label,
            value: e.value,
            children: []
          })
          e.children.forEach(n => {
            this.searchData[1].option[i].children.push({
              label: n.label,
              value: n.value
            })
          })
        })
      },
      // 点击添加学生
      onDialogClick () {
        this.dialogVisible = true
        this.studentForm = {
          subjectId: null,
          studentId: [],
        }
        this.selectedName = []
        this.getStudent()
        this.getClassCascade()
      },
      // 竞赛科目维护
      async onSubjectClick () {
        this.dialogSubjectVisible = true
      },
      // 删除竞赛科目
      async removeSubject (item) {
        await this.$confirm(`您确认删除【${item.label}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/competitionSubject/deleteCompetitionSubject.do',
          query: {
            id: item.value,
          }
        })
        this.$remind('删除成功')
        await this.onSubjectList()
      },
      // 新增竞赛科目
      async onSubmitSubject () {
        let url = '/competitionSubject/addCompetitionSubject.do'
        await this.$ajax({
          url,
          query: {
            // 名称
            subjectName: this.newSubject
          }
        })
        await this.onSubjectList()
        this.$remind('新增科目成功')
        this.subjectSwitch = true
      },
      // 编辑
      async onSubmitEdi (item) {
        let url = '/competitionSubject/updateCompetitionSubject.do'
        await this.$ajax({
          url,
          query: {
            id: item.value,
            // 名称
            subjectName: this.ediSubject
          }
        })
        this.$remind('修改科目成功')
        await this.$search(this.table)
        await this.onSubjectList()
        this.ediSubject = null
      },
      // 删除
      async remove (item) {
        console.log(item)
        await this.$confirm(`您确认删除【${item.studentName}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/competition/deleteCompetition.do',
          query: {
            id: item.id,
          }
        })
        this.$remind('删除成功')
        await this.$search(this.table)
      },

    },
    mounted () {
      this.onGetGradeList()
      this.onSubjectList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btns
      position: absolute
      right: 15px
      top: 6px
      z-index: 9
    .pagination
      text-align: center
      margin-top: 10px
    .btns_two
      margin-top: 20px
      text-align: center
    .add
      color: $main-c
      cursor: pointer
    .dialog
      .input
        width: 100%
    .search-box
      margin-top: 10px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px
</style>
